export default (sequelize, Sequelize) => {
  const ExpectedLearningOutcomes = sequelize.define(
    "expected_learning_outcome",
    {
      description_th: {
        type: Sequelize.TEXT,
      },
      description_en: {
        type: Sequelize.TEXT,
      },
    }
  );
  return ExpectedLearningOutcomes;
};
